*{
  margin:0;
  padding:0;
}body{
  background: url("https://tse3.mm.bing.net/th?id=OIP.7MFQuRCI-acpOwwAYKX2GgHaEo&pid=Api&P=0&h=220");
  font-family:" Bree serif ","serif";
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}.heading{
  text-align: center;
  color:#f71aa2;
}.app{
  background: linear-gradient(#f71aa2 0%,#6800ff 80%);
  width:90%;
  min-height: 200px;
  height: min-content;
  border-radius:15px;
  padding: 20px;
  box-shadow: 10px 10px  42px 0px rgba(0,0,0,0.75);
  display: flex;
  justify-content: space-evenly;
}.Question-section{
  position: relative;
  width:100%;

}.Question-count{
  color:blue;
  font-size: 20px;
  margin-bottom: 20px;
}.Question-text{
  font-size: 20px;
  color:brown;
}.Answer-Section{
  display:flex;
  flex-direction: column;
  width:100%;
  justify-content: space-between;
}button{
  width:100%;
  font-size: 10px;
  color:#ffffff;
  background:linear-gradient(83deg,#f71aa2 0%, #6800ff 80%);
  border-radius: 15px;
  display:flex;
  padding:10px;
  justify-content: flex-start;
  align-items: center;
  border:3px solid #234668;
  cursor:pointer;
}.score-section{
  text-align: center;
  color:white;
  font-size: 25px;
  
}